'use strict';
import React from 'react';
import {
    Panel,
    PanelHeader,
    ModalCard,
    ModalRoot,
    Avatar,
    UsersStack,
    ModalPage, PanelSpinner,
    ModalPageHeader,
    IOS,
    PanelHeaderButton,
    Cell,
    List,
    platform,
    View,
    Spinner,
    PanelHeaderContent, Button, Link, ScreenSpinner,
} from '@vkontakte/vkui';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24ShareOutline from '@vkontakte/icons/dist/24/share_outline';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';

import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import connect from "@vkontakte/vkui-connect";
import set from "@babel/runtime/helpers/esm/set";

const osname = platform();
let myMap=null
let obt=null
export default class Persik extends React.Component {

    constructor(props){
        super(props);
        this.state={
            activeModal:null,title:"",type:null,starttime:"",img:null,modalHistory: [],scheme:"bright_light",igo:0,eid:0,popout:null,
        }
        this.modalBack = () => {
            this.setActiveModal(this.state.modalHistory[this.state.modalHistory.length - 2]);

        };
    }
    setActiveModal(activeModal) {
        activeModal = activeModal || null;
        let modalHistory = this.state.modalHistory ? [...this.state.modalHistory] : [];

        if (activeModal === null) {
            modalHistory = [];
        } else if (modalHistory.indexOf(activeModal) !== -1) {
            modalHistory = modalHistory.splice(0, modalHistory.indexOf(activeModal) + 1);
        } else {
            modalHistory.push(activeModal);
        }

        this.setState({
            activeModal,
            modalHistory
        });
    };
    UNSAFE_componentWillMount() {
        if(document.body.attributes.getNamedItem("scheme")!==null){
            this.setState({scheme:document.body.attributes.getNamedItem("scheme").value})
        }
    }

    componentDidMount() {
        const {token} = this.props
       const ymaps = window.ymaps
       ymaps.ready(()=> {
            myMap = new window.ymaps.Map("map", {
                center: [55.76, 37.64],
                controls: [],
                zoom: 15
            });
           var customItemContentLayout = ymaps.templateLayoutFactory.createClass(
               //  "raw" ,    " "   html.


           );

           var loadingObjectManager  = new ymaps.LoadingObjectManager ('https://api.finebot.site/events/events.tile?bbox=%b&token='+token,
               {
                   //      cluster.
                   clusterize: true,
                   clusterDisableClickZoom: false,
                   clusterOpenBalloonOnClick: false,
                   //      cluster.
                   clusterHasBalloon: true,

                   //      geoObject.
                   geoObjectOpenBalloonOnClick: false
               });



           myMap.geoObjects.add(loadingObjectManager);





           function hasBalloonData1 (objectId) {
               return loadingObjectManager.clusters.getById(objectId).properties.balloonContent;
           }
           loadingObjectManager.objects.events.add('click', (e)=> {
               var objectId = e.get('objectId'),
                   obj = loadingObjectManager.objects.getById(objectId);
               this.setState({title:""})

               setTimeout(()=>{this.startloading()})
                    //alert(JSON.stringify(obj))
               var xhr = new XMLHttpRequest();

               console.log("Stage -1")
               xhr.open('GET', "https://api.finebot.site/events/events.getmini?token="+token+"&eid="+obj.id, true);
               console.log("Stage 0")
               xhr.send();
               console.log("Stage 0,5")

               xhr.onreadystatechange=()=> {
                   setTimeout(() => {
                       if (xhr.readyState != 4) return;
                       var t=xhr.responseText
                       t=JSON.parse(t)
                       setTimeout(()=>this.setActiveModal("fully"),210)
                       setTimeout(()=>{this.stoploading()})

                       this.setState({title:t.response.title,type:t.response.type,starttime:this.unix(t.response.unixtime),img:t.response.img,igo:t.response.igo,eid:obj.id})
                   })
               }
                    //this.setState({title:obj.properties.title,type:obj.properties.type,starttime:this.unix(obj.properties.unixtime),img:obj.properties.img,igo:obj.properties.igo,eid:obj.id})
                   //loadingObjectManager.objects.balloon.open(objectId);

           });

           loadingObjectManager.clusters.events.add('mouseenter', (e)=> {
               var objectId = e.get('objectId'),
                   obj = loadingObjectManager.clusters.getById(objectId);
               if (myMap.getZoom()>=18) {
                   //alert(JSON.stringify(obj.features))
                   obt=obj.features
                   this.setActiveModal("alleventts")


               }
           });








       });
    }
    ret(e){
        const {token} = this.props

        return <Cell onClick={()=>{
            this.setState({title:""})

            setTimeout(()=>{this.startloading()})

            //alert(JSON.stringify(obj))
            var xhr = new XMLHttpRequest();

            console.log("Stage -1")
            xhr.open('GET', "https://api.finebot.site/events/events.getmini?token="+token+"&eid="+e.id, true);
            console.log("Stage 0")
            xhr.send();
            console.log("Stage 0,5")

            xhr.onreadystatechange=()=> {
                setTimeout(() => {
                    if (xhr.readyState != 4) return;
                    var t=xhr.responseText
                    t=JSON.parse(t)
                    setTimeout(()=>this.setActiveModal("fully"),210)
                    setTimeout(()=>{this.stoploading()})

                    this.setState({title:t.response.title,type:t.response.type,starttime:this.unix(t.response.unixtime),img:t.response.img,igo:t.response.igo,eid:e.id})
                })
            }
        }} before={<Avatar src={e.properties.img} />}>{e.properties.title}</Cell>
    }
    unix(time){
        var now = new Date();
        var start = new Date(time*1000);
        var hr=start.getHours()
        if(hr<10){hr="0"+hr}
        var mi=start.getMinutes()
        if(mi<10){mi="0"+mi}
        var rt="  "+hr+":"+mi

        if(now.getDate()==start.getDate()&&now.getFullYear()==start.getFullYear()&&now.getMonth()==start.getMonth()){
            rt=","+rt
        }else{
            now.setDate(now.getDate()+1)
            if(now.getDate()==start.getDate()&&now.getFullYear()==start.getFullYear()&&now.getMonth()==start.getMonth()){
                rt=","+rt
            }else{
                var dt=start.getDate()
                if(dt<10){dt=0+""+dt}
                var mn=start.getMonth()+1
                if(mn<10){mn=0+""+mn}

                rt=dt+"."+mn+"."+start.getFullYear()+","+rt
            }
        }

        return rt
    }
    getRetButtons(i,id){
        var buttons=null
        var igo=this.state.igo
        // console.log(i+" "+decodeURIComponent(this.state.events[i]))
        var colorbt="#5181B8"

        if(document.body.attributes.getNamedItem("scheme")==null||document.body.attributes.getNamedItem("scheme")==undefined){
        }
        else if(document.body.attributes.getNamedItem("default_theme")==null){
        }else if(document.body.attributes.getNamedItem("default_theme").value==null){
        }
        else if(document.body.attributes.getNamedItem("default_theme").value==="null"){
        }else{
            switch (document.body.attributes.getNamedItem("scheme").value) {
                case 'client_light':
                    colorbt="#5181B8"

                    break;
                case 'client_dark':
                    colorbt="#E1E3E6"


                    break;
                case 'bright_light':
                    colorbt="#4986CC"


                    break;
                case 'space_gray':
                    colorbt="#E1E3E6"



                    break;
            }
        }
        if(igo+""==="1"){
            buttons=(

                <div style={{display: 'flex',marginLeft:"15px",marginRight:"15px",marginBottom:"10px",marginTop:"10px"}}>

                    <Button size="l" id="igobt" className="noselect" stretched style={{ marginRight: 8,cursor:"pointer" }} mode="outline" onClick={()=>{setTimeout(()=>{

                        this.igo()})}}> </Button>
                    <div  style={{alignItems:"center",marginTop:"auto",marginBottom:"auto",marginLeft:"3px",marginRight:"5px"}}><Link size="l" mode="tertiary" stretched onClick={()=>connect.send("VKWebAppShare", {"link": "https://vk.com/appevents#e"+this.state.eid})} style={{cursor:"pointer"}}><Icon24ShareOutline style={{color: colorbt}}/></Link></div>

                </div>

            );

        }else if(igo+""==="0"){

            buttons=(

                <div style={{display: 'flex',marginLeft:"15px",marginRight:"15px",marginBottom:"10px",marginTop:"10px"}}>
                    <Button size="l" stretched id="igobt" className="noselect" style={{ marginRight: 8,cursor:"pointer" }} onClick={()=>{setTimeout(()=>{

                        this.igo()})}}> </Button>
                    <div  style={{alignItems:"center",marginTop:"auto",marginBottom:"auto",marginLeft:"3px",marginRight:"5px"}}><Link size="l" mode="tertiary" stretched onClick={()=>connect.send("VKWebAppShare", {"link": "https://vk.com/appevents#e"+this.state.eid})} style={{cursor:"pointer"}}><Icon24ShareOutline style={{color: colorbt}}/></Link></div>
                </div>


            );
        }else{
            buttons=igo
        }

        return buttons;

    }
    startloading(){
        this.setState({popout:<ScreenSpinner />,activeModal:null})
//		style={{backgroundColor:this.state.schemecolor}}
    }
    stoploading(){
        setTimeout(()=>this.setState({popout:null}),150)
    }
    async igo(){
        const {token,reload} = this.props
        document.getElementById("igobt").disabled=true
        reload.call()
        if (this.state.igo==1){
            this.setState({igo:0})
            this.geta("https://api.finebot.site/events/account.noigo?token="+token+"&id="+this.state.eid)

        }else{
            this.setState({igo:1})
            this.geta("https://api.finebot.site/events/account.igo?token="+token+"&id="+this.state.eid)


        }
        document.getElementById("igobt").disabled=false

    }
    geta(url){
        var xhr = new XMLHttpRequest();
        xhr.open('POST', url, true);
        xhr.send();

        return xhr.responseText;
    }
    render() {
        var img=(
            <div>{this.state.title===""?(null):( <div style={{width:"30vw",height:"15vh",marginLeft:"auto",marginRight:"auto",marginBottom:"10px"}}>
                <div id="loadspfullmap"><Spinner size="large" style={{width:"30vw",height:"15vh",marginLeft:"auto",marginRight:"auto",position:"absolute"}} /></div>
                <div><img onLoad={()=>document.getElementById("loadspfullmap").style.display='none'} className="imgret" style={{ width:"30vw",marginBottom:"10px",marginLeft:"auto",marginRight:"auto",display:"block"}} src={this.state.img}/></div>
            </div>)}</div>


        )

        const modal=(
            <ModalRoot activeModal={this.state.activeModal}>

                <ModalPage
                    id="alleventts"
                    onClose={this.modalBack}
                    header={
                        <ModalPageHeader
                            right={<PanelHeaderButton onClick={this.modalBack}><Icon24Cancel /></PanelHeaderButton>}
                        >
                            
                        </ModalPageHeader>
                    }
                >
                    <List>
                        {obt==null?(null):(obt.map(this.ret.bind(this)))}
                    </List>
                </ModalPage>


                <ModalCard
                    id="fully"
                    onClose={() =>this.modalBack()}
                    icon={img}
                    header={this.state.title}
                    caption={<div>{this.state.title===""?(null):(<div>{this.state.type}<br/>: {this.state.starttime}</div>)}</div>}

                >

                    {this.state.title===""?(<div><Spinner size="large" style={{width:"30vw",height:"15vh",marginLeft:"auto",marginRight:"auto"}} /></div>):(<div>{this.getRetButtons()}</div>)}
                </ModalCard>
                <ModalCard
                    id="load"
                    onClose={() =>null}


                >

                   <div><Spinner size="large" style={{width:"30vw",height:"15vh",marginLeft:"auto",marginRight:"auto"}} /></div>
                </ModalCard>

            </ModalRoot>
                );


        return (
            <View separator={false} id="fullmap" activePanel="fl" modal={modal} popout={this.state.popout}>
                <Panel separator={false} id="fl">
                    <PanelHeader
                        left={<PanelHeaderButton style={{cursor:"pointer"}} onClick={()=>window.history.back()} data-to="home">{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</PanelHeaderButton>}
                        addon={<PanelHeaderButton onClick={()=>window.history.back()}></PanelHeaderButton>}
                    >

                         

                    </PanelHeader>

                    {this.state.scheme==="bright_light"?(<div id="map" style={{width:"100%", height:"93vh"}}/>):(<div id="map" className="ymaps-layers-pane" style={{width:"100%", height:"93vh"}}/>)}
                </Panel>
            </View>
        );
    }
}



